import React, { useState, useEffect } from 'react'
import API from './API'
import Table from './Table'
import Thanks from './Thanks'
import { useTranslation } from 'react-i18next'
import { toast } from 'react-toastify'
import { t } from 'i18next'
import { Dialog } from '@mui/material'
import { Button } from '@material-ui/core'

function Layout({restaurantId, date, email, peopleCount, height, width}) {

	const { i18n } = useTranslation()
	const [tables, setTables] = useState([])
	const [freeTables, setFreeTables] = useState([])
	const [background, setBackground] = useState(null)
	const [selectedTableId, setSelectedTableId] = useState(null)
	const [layoutSize, setLayoutSize] = useState({x: 0, y: 0})
	const [done, setDone] = useState(false)

	useEffect(() => {
		API.get('/api/layouts/' + restaurantId).then(response => {
			setBackground(response.data.message.backgroundImage)
			setLayoutSize({x: response.data.message.sizeX, y: response.data.message.sizeY})
			setTables(response.data.message.map((table) => {
	            return {
	                databaseID: table.TableId,
	                coordinates: table.coordinates,
	                type: table.tableType,
	                localId: table.localId,
	                rounded: table.tableType === "rounded",
	                size: table.size,
	                direction: table.direction,
	                tableCount: table.tableCount
	            }
        	}))
		}).catch(err => {
			setTables([])
		})
	}, [])

	useEffect(() => {
		API.post('/api/appointments/search-tables', {
			restaurantId: restaurantId,
			date: date,
			peopleCount: peopleCount,
			email: email
		}).then(freeTables => {
			console.log(freeTables.data.message)
			setFreeTables(freeTables.data.message)
		})
	}, [date])

	const openConfirmal = (id, tableCount) => {
		if(peopleCount > tableCount) {
			toast.error(t('all.peoplecount-too-much'))
			return
		}
		setSelectedTableId(id)
	}

	const bookTable = () => {
		API.post('/api/appointments/book', {
			restaurantId: restaurantId,
			tableId: selectedTableId,
			date: date,
			email: email,
			peopleCount: peopleCount,
			lang: i18n.language
		}).then((response) => {
			setSelectedTableId(null)
			setDone(true)
		}).catch(err => {
			toast.error(t('all.error-while-booking'))
		})
	}


	return (
		<div style={{height: !done ? height : 'auto', width: !done ? width : 'auto',  borderWidth: !done ? '1px' : '0px', borderColor: 'black', borderStyle: 'solid'}}>
			<div style={{position: "relative", height: '100%'}}>
				{
					!done ?
						<div style={{width: layoutSize.x, height: layoutSize.y, backgroundImage: `url(https://192.168.31.214:4000/backgrounds/${background})`, backgroundSize: 'cover', backgroundRepeat: 'no-repeat', backgroundPosition: 'center'}}>
						{
							tables.map((table) => (
								<div key={table.localId}>
									<Table
										id={table.localId}
										rounded={table.rounded} 
										tableCount={table.tableCount} 
										type={table.type} 
										direction={table.direction}
										coordinates={table.coordinates}
										size={table.size}
										editable={false}
										bookTable={() => openConfirmal(table.databaseID, table.tableCount)}
										inUse={freeTables.find(t => t.id === table.databaseID)?.type}
										/>
								</div>
							))
						}
						</div>
						:
					<Thanks />
				}
			</div>
			<Dialog disableEnforceFocus onClose={() => setSelectedTableId(null)} open={selectedTableId !== null}>
				<div className="p-4">
					<h3 className="pb-4 pt-2 text-center">
						{t('all.sure-to-book')}
					</h3>

					<div className="d-flex justify-content-between">
						<Button variant="contained" color="primary" onClick={bookTable}>{t('all.approve')}</Button>
						<Button variant="outlined" color="secondary" onClick={() => setSelectedTableId(null)}>{t('all.cancel')}</Button>
					</div>
				</div>
			</Dialog>
		</div>
	)

}

export default Layout