import './App.css';
import { useEffect, useState } from 'react'
import CredentialsForm from './components/CredentialsForm'
import Layout from './components/Layout'
import API from './components/API'
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import './localization/translation'
import { useTranslation } from 'react-i18next'
import Thanks from './components/Thanks';

function App({restaurantId}) {

  const [layoutSize, setLayoutSize] = useState({x: 0, y: 0})
  const [error, setError] = useState(false)
  const [t, i18n] = useTranslation();
  const [credentialsEntered, setCredentialsEntered] = useState(false)
  const [quickBook, setQuickBook] = useState(false)
  const [date, setDate] = useState(null)
  const [data, setData] = useState({email: '', peopleCount: 0})
  const languages = ['en', 'hu']

  useEffect(() => {
    const browserLanguage = navigator.language || navigator.userLanguage
    i18n.changeLanguage(languages.includes(browserLanguage) ? browserLanguage : 'en')
    API.get('/api/layouts/' + restaurantId + '/data').then(response => {
      setError(false)
      setLayoutSize({x: response.data.message.sizeX, y: response.data.message.sizeY})
    }).catch(err => {
      setError(true)
      t('all.no-restaurant-found')
    })
  }, [])

  const clickThrough = async(email, peopleCount, automaticTable) => {

    console.log(automaticTable)
    await API.post('/api/appointments/is-open', {restaurantId, date}).then(async () => {
      setData({email, peopleCount})
      if(automaticTable) {
        await API.post('/api/appointments/book', {
          restaurantId: restaurantId,
          tableId: 'any',
          date: date,
          email: email,
          peopleCount: peopleCount,
          lang: i18n.language
        }).then((response) => {
          setCredentialsEntered(true)
          setQuickBook(true)
        }).catch(err => {
          toast.error(t('all.error-while-booking'))
        })
      }else{
        setCredentialsEntered(true)
      }
    }).catch(err => {
      console.log(err)
      toast.error(t('all.error-while-booking'))
    })

  }

  return (
    <div className="App">
      {
        !error ? (
          !credentialsEntered ?
            <CredentialsForm restaurantId={restaurantId} clickThrough={clickThrough} date={date} setDate={setDate} />
            :
          (!quickBook ? 
              <Layout restaurantId={restaurantId} date={date} email={data.email} peopleCount={data.peopleCount} height={layoutSize.y} width={layoutSize.x} />
              : <Thanks /> ))
          :
        (
          <div className="text-center p-4">
            <h3>{t('all.cant-load')}</h3>
          </div>
        )
      }
      <ToastContainer />
    </div>
  );
}

export default App;