import React from 'react'
import { TextField, Button, FormControl } from '@material-ui/core'
import AdapterDateFns from '@mui/lab/AdapterDateFns';
import LocalizationProvider from '@mui/lab/LocalizationProvider';
import DateTimePicker from '@mui/lab/DateTimePicker';
import { toast } from 'react-toastify';
import { t } from 'i18next';

function CredentialsForm({clickThrough, date, setDate}) {

	const submitData = (e) => {

		e.preventDefault()

		const email = e.target.elements.email.value
		const peopleCount = Number(e.target.elements.peopleCount.value)

		if(!(/^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/.test(email))){
            toast.error(t('all.email-format-error'))
            return
        }

		if(peopleCount < 1) {
			toast.error(t('all.peoplecount-error'))
			return
		}
        
        if(isNaN(Date.parse(date))) {
            toast.error(t('all.date-format-error'))
            return    
        }

		console.log(e.target.table.checked)
		clickThrough(email, peopleCount, e.target.table.checked)
	}

	return (
		<div className="h-100 w-100 d-flex justify-content-center flex-column justify-content-center">
			<div className="text-center d-flex flex-column align-items-center justify-content-center">
				<form onSubmit={submitData}>
					<TextField style={{width: '350px'}} className="m-2" name="email" type="email" placeholder={t('all.email')} />
					<br />
					<TextField style={{width: '350px'}} className="m-2" name="peopleCount" type="number" min="0" placeholder={t('all.peoplecount')} />
					<br />
					<LocalizationProvider style={{width: '350px'}} dateAdapter={AdapterDateFns}>
		                <DateTimePicker
							minDate={new Date()}
		                    renderInput={(props) => <TextField name="date" {...props} />}
		                    label={t('all.select-date')}
		                    ampm={false}
		                    value={date}
		                    onChange={(newValue) => {
		                        setDate(newValue)
		                    }}
		                />
	                </LocalizationProvider>
	                <br />
					<div className="d-flex align-items-center justify-content-center mt-4">
						<p className='m-0'>
							{t('all.automatic-table-selecting')}:
						</p>
						<TextField name="table" style={{width: 'auto', paddingLeft: '10px'}} type={"checkbox"} InputProps={{disableUnderline: true}}></TextField>
					</div>
					<br />
					<Button type="submit" className="m-2" variant="outlined">{t('all.next')}</Button>
				</form>
			</div>
		</div>
	)

}

export default CredentialsForm