import { t } from 'i18next'
import React from 'react'

function Thanks() {

	return (
		<div className="d-flex h-100 justify-content-center flex-column p-3">
			<div className="text-center">
				<h4>
					{t('all.thanks')}
				</h4>
				<img width="100px" src="/done.png" />
			</div>
		</div>
	)
}

export default Thanks